@import '1-abstracts/variables';

// @import '2-vendors/materialize.scss';

@import '3-base/base';

@import '4-layout/header';
$theme-red: #d7263d;
$theme-darkblue: #02182b;
$theme-blue: #0197f6;
$theme-green: #04e762;
$theme-yellow: #f5b700;

$font-stack: 'Inconsolata', monospace;

* {
  margin: 0;
  padding: 0;
  // box-sizing: border-box;
}

html {
  font-family: $font-stack;
}

a {
  cursor: pointer;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 80px;
}


header {
  position: absolute;
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;

  color: red;
}

nav {
  ul {
    list-style: none;
    color: red;
  }
  li {
    color: red;
  }
  a {
    color: red;
  }
}